<template>
  <div>
    <div class="headerTextB">Search User Information</div>
    <b-row class="mt-3 d-flex align-items-center">
      <b-col cols="12" xl="6" id="options">
        <label>Select Type Option</label>
        <b-form-select
          v-model="selected_type_option"
          :options="typeoption"
          value-field="item"
          text-field="name"
          disabled-field="notEnabled"
        >
        </b-form-select>
      </b-col>
      <b-col id="options" cols="12" xl="6" class="mt-4 alignButtons">
        <b-button @click="getDonarData" variant="primary">Search</b-button>
      </b-col>
    </b-row>
    <br />

    <div class="mt-5">
      <b-row>
        <b-col cols="12" xl="6">
          <label class="headerTextB">Information List</label>
        </b-col>
        <b-col cols="12" xl="6" class="float-right alignButtons">
          <b-button
            @click="showRegisterModal"
            variant="primary"
            class="float-right"
            >Register Info</b-button
          >
        </b-col>
      </b-row>
    </div>
    <div class="mt-4">
      <b-table
        id="infoTable"
        ref="selectableTable"
        :key="indexRefresh"
        :select-mode="selectModeTable"
        :fields="fields"
        :items="DonarData"
        :striped="true"
        :bordered="false"
        :borderless="false"
        head-variant="dark"
        fixed
        sticky-header
      >
        <template v-slot:cell(type_name)="item">{{ item.value }}</template>
        <template v-slot:cell(title)="item">{{ item.value }}</template>
        <template v-slot:cell(details)="item">{{ item.value }}</template>
        <template v-slot:cell(Action)="item">
          <i
            @click="deleteDetails(item)"
            class="fa fa-trash"
            aria-hidden="true"
          ></i>
          <i
            @click="edit(item)"
            class="fa fa-pencil-square-o ml-1"
            aria-hidden="true"
          ></i>
        </template>
      </b-table>
      <b-modal
        id="tree-modal1"
        ref="tree-modal1"
        :hide-footer="true"
        centered
        variant="secondary"
        modal-class="py-0"
        body-class="py-0"
        size="lg"
        footer-class="border-1"
        header-class="border-0 pd-0"
        button-size="sm"
      >
        <template v-slot:modal-header="{ cancel }">
          <span class="headerText mt-3 pl-3">Register User Information</span>
          <div class="w0">
            <button
              @click="cancel"
              type="button"
              aria-label="Close"
              class="close close-btn"
            >
              ×
            </button>
          </div>
        </template>
        <div class="line mt-2"></div>
        <b-card class="pop-up-box border-0">
          <b-row>
            <b-col cols="12" xl="6">
              <label>Admin Id</label>
              <input
                v-model="RegisterInfo.user_id"
                type="text"
                class="form-control"
                readonly
              />
              <show-errors
                :control="$v.RegisterInfo.user_id"
                :field="$t('user_id')"
              />
            </b-col>
            <b-col cols="12" xl="6" class="justifyMobile">
              <label>Select Type</label>

              <b-form-select
                v-model="RegisterInfo.type_id"
                :options="TypeOptions"
                value-field="item"
                text-field="name"
                disabled-field="notEnabled"
              >
              </b-form-select>
            </b-col>
          </b-row>
          <b-row class="mt-3">
            <b-col cols="12">
              <label>Title</label>
              <input
                v-model="RegisterInfo.title"
                type="text"
                class="form-control"
              />
              <show-errors
                :control="$v.RegisterInfo.title"
                :field="$t('title')"
              />
            </b-col>
          </b-row>
          <b-row class="mt-3">
            <b-col cols="12">
              <label>Details</label>
              <b-form-textarea
                id="textarea"
                v-model="RegisterInfo.details"
                placeholder="Enter Details..."
                rows="3"
                max-rows="6"
              ></b-form-textarea>
              <show-errors
                :control="$v.RegisterInfo.details"
                :field="$t('details')"
              />
            </b-col>
          </b-row>
        </b-card>
        <div class="line"></div>
        <b-row>
          <b-col cols="12" class="mb-2 mt-2 d-flex justify-content-end">
            <b-button @click="close" variant="secondary" class="mr-1"
              >close</b-button
            >
            <b-button @click="RegisterData" variant="primary">Add</b-button>
          </b-col>
        </b-row>
      </b-modal>

      <b-modal
        id="tree-modal2"
        ref="tree-modal2"
        :hide-footer="true"
        centered
        variant="secondary"
        modal-class="py-0"
        body-class="py-0"
        size="lg"
        footer-class="border-1"
        header-class="border-0 pd-0"
        button-size="sm"
      >
        <template v-slot:modal-header="{ cancel }">
          <span class="headerText mt-3 pl-3">Update User Information</span>
          <div class="w0">
            <button
              @click="cancel"
              type="button"
              aria-label="Close"
              class="close close-btn"
            >
              ×
            </button>
          </div>
        </template>
        <div class="line mt-2"></div>
        <b-card class="pop-up-box border-0">
          <b-row>
            <b-col cols="12" xl="6">
              <label>Admin Id</label>
              <input
                v-model="UpdateInfo.user_id"
                type="text"
                class="form-control"
                readonly
              />
              <show-errors
                :control="$v.UpdateInfo.user_id"
                :field="$t('user_id')"
              />
            </b-col>
            <b-col cols="12" xl="6" class="justifyMobile">
              <label>Select Type</label>

              <b-form-select
                v-model="UpdateInfo.type_id"
                :options="TypeOptions"
                value-field="item"
                text-field="name"
                disabled-field="notEnabled"
              >
              </b-form-select>
            </b-col>
          </b-row>
          <b-row class="mt-3">
            <b-col cols="12">
              <label>Title</label>
              <input
                v-model="UpdateInfo.title"
                type="text"
                class="form-control"
              />
              <show-errors
                :control="$v.UpdateInfo.title"
                :field="$t('title')"
              />
            </b-col>
          </b-row>
          <b-row class="mt-3">
            <b-col cols="12">
              <label>Details</label>
              <b-form-textarea
                id="textarea"
                v-model="UpdateInfo.details"
                placeholder="Enter Details..."
                rows="3"
                max-rows="6"
              ></b-form-textarea>
              <show-errors
                :control="$v.UpdateInfo.details"
                :field="$t('details')"
              />
            </b-col>
          </b-row>
        </b-card>
        <div class="line"></div>
        <b-row>
          <b-col cols="12" class="mb-2 mt-2 d-flex justify-content-end">
            <b-button @click="close" variant="secondary" class="mr-1"
              >close</b-button
            >
            <b-button @click="UpdateData" variant="primary">Update</b-button>
          </b-col>
        </b-row>
      </b-modal>
    </div>
  </div>
</template>

<script>
import { Settings } from "luxon";
import { required, email, numeric, minLength } from "vuelidate/lib/validators";
import ShowErrors from "@/components/ShowErrors";
Settings.defaultLocale = "ja";
import axios from "axios";
import { ApiServicesFactory } from "@/shared/apiServiceFactory";
const BasicInfoApis = ApiServicesFactory.get("basic");

export default {
  name: "Index",
  components: { ShowErrors },
  data() {
    return {
      indexRefresh: 1,
      selected_type_option: "",
      typeoption: [],
      selectedBloodGroup: "",
      selectedCountry: "",
      selectedState: "",
      selectedDist: "",
      selectedCity: "",
      bloodGroupOption: [],
      CountryOption: [],
      TypeOptions: [],
      typeData: {},
      StateOption: [],
      DistOption: [],
      CityOption: [],
      DonarData: [],
      tableData: [],
      countryData: [],
      bloodGroupData: [],
      bloodGroup_Option: [],
      Country_Option: [],
      State_Option: [],
      District_Option: [],
      City_Option: [],
      selectModeTable: "single",
      plasmadonar_Option: [
        { item: "0", name: "Yes" },
        { item: "1", name: "No" },
      ],
      RegisterInfo: {
        user_id: localStorage.getItem("user_id"),
        type_id: "",
        title: "",
        details: "",
        type_name: "",
      },
      UpdateInfo: {
        user_id: localStorage.getItem("user_id"),
        type_id: "",
        title: "",
        details: "",
        type_name: "",
      },
    };
  },
  computed: {
    fields() {
      return [
        {
          key: "type_name",
          label: "Type",
          class: "text-center normal-width",
        },
        {
          key: "title",
          label: "Title",
          class: "text-center normal-width",
        },
        {
          key: "details",
          label: "Details",
          class: "text-center normal-width",
        },
        {
          key: "Action",
          label: "Action",
          class: "text-center normal-width",
        },
      ];
    },
  },
  validations: {
    RegisterInfo: {
      user_id: { required },
      type_id: { required },
      title: { required },
      details: { required },
    },
    UpdateInfo: {
      user_id: { required },
      type_id: { required },
      title: { required },
      details: { required },
    },
  },
  mounted() {
    this.serviceGetFunction();
    this.getBloodGroupData();
    this.getTypeData();
  },
  created() {},
  methods: {
    async deleteDetails(item) {
      console.log("delete item", item);
      let url = `master/delete/details`;
      let Data = await BasicInfoApis.postAPI(url, {
        key: "id",
        value: item.item.id,
      });
      if (Data.data.success) {
        this.serviceGetFunction();
      }
    },
    async UpdateData() {
      this.$v.UpdateInfo.$touch();
      if (!this.$v.UpdateInfo.$invalid) {
        let payLoad = {
          data: [
            {
              user_id: localStorage.getItem("user_id"),
              type_id: this.UpdateInfo.type_id,
              title: this.UpdateInfo.title,
              details: this.UpdateInfo.details,
            },
          ],
          conditions: [
            {
              key: "id",
              value: this.UpdateInfo.id,
            },
          ],
        };
        let url = `master/update/details`;
        let Data = await BasicInfoApis.postAPI(url, payLoad);
        if (Data.data.success) {
          this.serviceGetFunction();
          this.$refs["tree-modal2"].hide();
        }
      }
    },

    async RegisterData() {
      this.$v.RegisterInfo.$touch();
      if (!this.$v.RegisterInfo.$invalid) {
        let payLoad = {
          user_id: localStorage.getItem("user_id"),
          type_id: this.RegisterInfo.type_id,
          title: this.RegisterInfo.title,
          details: this.RegisterInfo.details,
        };
        let url = `master/createData/details`;
        let Data = await BasicInfoApis.postAPI(url, payLoad);
        console.log(Data, "DataData");
        if (Data.data.success) {
          this.serviceGetFunction();
          this.$refs["tree-modal1"].hide();
        }
      }
    },
    edit(item) {
      this.UpdateInfo.id = item.item.id;
      this.UpdateInfo.type_id = item.item.type_id;
      this.UpdateInfo.user_id = item.item.user_id;
      this.UpdateInfo.title = item.item.title;
      this.UpdateInfo.details = item.item.details;

      this.$refs["tree-modal2"].show();
    },
    showRegisterModal() {
      this.$refs["tree-modal1"].show();
    },
    close() {
      this.$refs["tree-modal1"].hide();
      this.$refs["tree-modal2"].hide();
    },
    getDonarData() {
      let payload = {
        paginate: false,
        limit: 100,
        fieldlist: [
          "details.id",
          "details.title",
          "details.user_id",
          "details.details",
          "types.name as type_name",
          "details.type_id",
        ],
        joins: [
          {
            table: "types",
            join_type: "left",
            join_conditions: [
              {
                join_using: "types.id",
                join_operator: "=",
                join_with: "details.type_id",
              },
            ],
          },
        ],
        conditions: [
          {
            key: "details.type_id",
            operator: "=",
            value: this.selected_type_option,
          },
        ],
      };
      let url = `https://119.82.96.67:8082/api/master/readwithjoin/details`;
      this.serviceFunction(payload, url);
    },
    async getBloodGroupData() {
      //http://119.82.96.67:8082/api/master/readall/countries
      let url = `master/readall/blood_groups`;
      const { data } = await BasicInfoApis.getAPI(url);
      console.log(data, "data2");
      this.bloodGroupOption.push({
        item: "",
        name: "Select",
        disabled: true,
      });
      this.bloodGroup_Option.push({
        item: "",
        name: "Select",
        disabled: true,
      });
      this.bloodGroupData = data.data;
      for (let index = 0; index < this.bloodGroupData.length; index++) {
        this.bloodGroupOption.push({
          item: this.bloodGroupData[index].id,
          name: this.bloodGroupData[index].blood_type,
        });
      }
      for (let index = 0; index < this.bloodGroupData.length; index++) {
        this.bloodGroup_Option.push({
          item: this.bloodGroupData[index].id,
          name: this.bloodGroupData[index].blood_type,
        });
      }
      console.log("this.bloodGroupOption", this.bloodGroupOption);
    },
    async getTypeData() {
      //http://119.82.96.67:8082/api/master/readall/countries
      let url = `master/readall/types`;
      const { data } = await BasicInfoApis.getAPI(url);
      console.log(data, "data2");
      this.typeData = data.data;
      this.typeoption.push({ item: "", name: "Select" });
      this.TypeOptions.push({ item: "", name: "Select" });

      this.typeData.map((elem) => {
        this.typeoption.push({ item: elem.id, name: elem.name });
        this.TypeOptions.push({ item: elem.id, name: elem.name });
      });
      // this.typeoption = this.typeData.map((elem) => {
      //   return { item: elem.id, name: elem.name };
      // });

      console.log("typeoption", this.TypeOptions);
    },
    async getStateDetails() {
      this.State_Option = [];
      console.log(this.RegisterInfo, "KKKKKK");
      alert(
        "this.RegisterInfo.selected_Country",
        this.RegisterInfo.selected_Country
      );
      let url = `getstates?country_id=${this.RegisterInfo.selected_Country}`;
      const { data } = await BasicInfoApis.getAPI(url);
      console.log(data, "data2");
      let StateData = data;
      console.log("StateData", StateData.length);
      this.StateOption.push({
        item: "",
        name: "Select",
        disabled: true,
      });
      for (let index = 0; index < data.length; index++) {
        this.State_Option.push({
          item: data[index].id,
          name: data[index].state_name,
        });
      }
    },
    async getStateData() {
      this.StateOption = [];
      console.log(this.selectedCountry, "selectedCountry");
      let url = `getstates?country_id=${this.selectedCountry}`;
      const { data } = await BasicInfoApis.getAPI(url);
      console.log(data, "data2");
      let StateData = data;
      console.log("StateData", StateData.length);
      this.countryData = data.data;
      this.StateOption.push({
        item: "",
        name: "Select",
        disabled: true,
      });
      for (let index = 0; index < data.length; index++) {
        this.StateOption.push({
          item: data[index].id,
          name: data[index].state_name,
        });
      }
    },
    async getDistDetails() {
      this.District_Option = [];
      let url = `getdistricts?state_id=${this.RegisterInfo.selected_State}`;
      const { data } = await BasicInfoApis.getAPI(url);
      console.log(data, "data2");
      this.District_Option.push({
        item: "0",
        name: "Select",
        disabled: true,
      });
      for (let index = 0; index < data.length; index++) {
        this.District_Option.push({
          item: data[index].id,
          name: data[index].district_name,
        });
      }
    },
    async getDistData() {
      this.DistOption = [];
      let url = `getdistricts?state_id=${this.selectedState}`;
      const { data } = await BasicInfoApis.getAPI(url);
      console.log(data, "data2");
      this.DistOption.push({
        item: "",
        name: "Select",
        disabled: true,
      });
      for (let index = 0; index < data.length; index++) {
        this.DistOption.push({
          item: data[index].id,
          name: data[index].district_name,
        });
      }
    },
    async getCityDetails() {
      this.City_Option = [];
      let url = `getcities?district_id=${this.RegisterInfo.selected_District}`;
      const { data } = await BasicInfoApis.getAPI(url);
      console.log(data, "data2");
      this.City_Option.push({
        item: "",
        name: "Select",
        disabled: true,
      });
      for (let index = 0; index < data.length; index++) {
        this.City_Option.push({
          item: data[index].id,
          name: data[index].city_name,
        });
      }
    },
    async getCityData() {
      this.CityOption = [];
      let url = `getcities?district_id=${this.selectedDist}`;
      const { data } = await BasicInfoApis.getAPI(url);
      console.log(data, "data2");
      this.CityOption.push({
        item: "",
        name: "Select",
        disabled: true,
      });
      for (let index = 0; index < data.length; index++) {
        this.CityOption.push({
          item: data[index].id,
          name: data[index].city_name,
        });
      }
    },
    async serviceGetFunction() {
      let url = "https://119.82.96.67:8082/api/master/readall/details";

      let data = await axios.get(url);
      console.log(data, "data");
      if (data.status == "200") {
        this.tableData = data.data.data;
        console.log(this.tableData, "DATTATATATAT");
      }
      let AllData;
      this.DonarData = [];
      this.tableData.forEach((data) => {
        AllData = {
          id: data.id, //blood_type,
          type_id: data.type_id,
          type_name: data.type_name,
          user_id: localStorage.getItem("user_id"),
          title: data.title,
          details: data.details,
        };
        this.DonarData.push(AllData);
      });
      this.indexRefresh = this.indexRefresh + 1;

      return data;
    },
    async serviceFunction(payLoad, url) {
      this.DonarData = [];
      let data = await axios.post(url, payLoad, {
        headers: {
          //   "Access-Control-Allow-Origin": true,
          //   Accept: "application/json",
          //   "Content-Type": "application/json",
        },
      });
      // if (data.data.success) {
      this.tableData = data.data.data.data;
      //   console.log(data, "data service funct");
      // }
      console.log(data, this.tableData, "data service funct");

      let AllData;
      this.tableData.forEach((data) => {
        AllData = {
          id: data.id, //blood_type,
          type_id: data.type_id,
          type_name: data.type_name,
          user_id: localStorage.getItem("user_id"),
          title: data.title,
          details: data.details,
        };
        this.DonarData.push(AllData);
      });
      return data;
    },
  },
};
</script>
<style>
.dashBoardFrmClass {
  background-color: #fff;
  border: none;
  border-radius: 8px;
  box-shadow: 0 0px 0px rgb(0 0 0 / 10%), 0 8px 16px rgb(0 0 0 / 10%);
  box-sizing: border-box;
}
.table .thead-dark th {
  background-color: #d14461;
  border-color: #d14461;
}
.headerText {
  font-size: 16px;
  font-weight: 700;
  color: #000;
}
.close-btn {
  background: #ffffff 0% 0% no-repeat padding-box !important;
  box-shadow: 0px 4px 10px #00000026 !important;
  color: #4b4b4b;
  border-radius: 50% !important;
  opacity: 1;
}
.modal-header .close {
  padding: 0.25rem 0.25rem;
  margin: -0.5rem -0.5rem -0.5rem auto;
}
.line {
  border-bottom: 2px solid #dadde1;
}
.headerTextB {
  background: #d14461;
  text-transform: uppercase;
  color: #fff;
  letter-spacing: 1px;
  font-size: 16px;
  font-weight: 300;
  transition: all 1s;
  cursor: default;
  padding: 10px 10px;
  max-width: 389px;
  display: flex;
  align-content: flex-start;
  justify-content: center;
}
@media only screen and (max-width: 773px) {
  .justifyMobile {
    margin-top: 15px !important;
  }
}
@media only screen and (max-width: 773px) {
  .justifyMobile {
    margin-top: 15px !important;
  }
  .alignButtons {
    display: flex !important;
    justify-content: center !important;
  }
  .alignContent {
    display: flex;
    justify-content: space-between;
    align-items: baseline;
  }
  .headerTextBdc {
    background: #d14461;
    text-transform: uppercase;
    color: #fff;
    letter-spacing: 1px;
    font-size: 16px;
    font-weight: 300;
    transition: all 1s;
    cursor: default;
    padding: 10px 10px;
    max-width: 389px;
    display: flex;
    align-content: flex-start;
    justify-content: center;
  }
  .table > tbody > tr > td {
    border: none !important;
  }
  thead {
    display: none !important;
  }
  td {
    display: inline-block !important;
  }
  #infoTable td[aria-colindex="2"],
  td[aria-colindex="3"],
  td[aria-colindex="4"] {
    display: block !important;
    position: relative !important;
    top: 8px !important;
    /* left: 75px !important; */
    /* width: 70% !important; */
    line-height: 1.5 !important;
    padding-left: 10px !important;
    border: none !important;
    padding-top: 2px !important;
    padding-bottom: 2px !important;
  }
  #infoTable td[aria-colindex="1"] {
    float: left !important;
    width: 80px !important;
    height: 80px !important;
    background-color: #d14461 !important;
    color: #fff !important;
    /* position: absolute !important; */
    left: 0 !important;
    top: 0 !important;
    bottom: 0 !important;
    line-height: 55px !important;
    margin: 10px 0 10px 12px !important;
    text-align: center !important;
    font-size: 25px !important;
  }

  .margincls {
    margin-left: -50px !important;
  }
}
@media only screen and (min-width: 993px) {
  .col-xl-1 {
    flex: 0 0 8.3333333333%;
    max-width: 6.333333% !important;
  }
  .blood_group_select {
    background: rgba(225, 225, 225, 0.1);
    margin: 5px 0;
    padding: 35px 0;
    font-family: Segoe, "Segoe UI", "DejaVu Sans", "Trebuchet MS", Verdana,
      sans-serif;
  }
}
</style>
